@import "../inc/config";

.screen_tables {
  flex-direction: column;

  li.table_line {
    display: flex;
    align-items: center;
    padding: 15px 5px;
    margin: 5px;
    background: $color1;
    border-radius: 15px;

    .num {
      width: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      text-transform: uppercase;
      font: 300 21px/21px $font;
      color: $color2;
    }

    .date {
      margin-left: 10px;
      text-transform: uppercase;
      letter-spacing: 1px;
      font: 600 12px/14px $font;
      color:$color3;
      display: flex;
      flex: 1;
    }

    .status {
      display: flex;
      width: 40px;

      i {
        color: $color2;
      }
    }
  }
}

.screen_tables_in {
  flex-direction: column;

  .bar {
    padding: 15px 25px 0;

    h1 {
      color: $color2;
      font: 400 14px/14px $font;

      span {
        font-weight: 500;
        color: #fff;
      }
    }
  }
  .times {
    display: flex;
    flex-wrap: wrap;
    margin:20px 0;
    .uk-margin {
      margin: 0 !important;
      width: 33.333%;
              padding-right: 10px;
      &:nth-child(3) {
        padding: 0;
      }
    }
  }
  form {
    padding: 20px;
  }
  button {
    margin-top: 10px;
  }
  label {
    color: #c2c2c2;
    font: 500 11px/11px $font;
    margin: 0 5px;
    letter-spacing: 1px;
    text-transform: uppercase;
  }

  input,
  select {
    margin-top: 10px;
    font: 400 13px/13px $font;
  }
}
